import BrowserWindow from 'sketch-module-web-view';
import CONFIG from './config';

const sketch = require('sketch');
const UI = require('sketch/ui');
const Settings = require('sketch/settings');
const Fiber = require('sketch/async');

const template = require('../resources/export.html');

export default function () {
  const document = require('sketch/dom').getSelectedDocument();

  const username = Settings.settingForKey(`${CONFIG.APP_KEY}_username`);
  const token = Settings.settingForKey(`${CONFIG.APP_KEY}_token`);

  if (document === undefined || !document) {
    UI.message('Please open document!');
    return false;
  }

  if (username === undefined || username === '' || token === undefined || token === '') {
    Settings.setSettingForKey(`${CONFIG.APP_KEY}_export_after_login`, true);
    require('./settings').default(); // eslint-disable-line
    return false;
  }

  const selectedLayers = toArray(document.selectedLayers);
  const filteredLayers = selectedLayers.filter(item => item.type === 'Artboard');

  if (filteredLayers.length === 0) {
    UI.message('Please select at least one artboard!');
    return false;
  } else {

    const options = {
      identifier: `${CONFIG.APP_KEY}.export`,
      alwaysOnTop: true,
      width: 576,
      height: 396,
      show: false,
      transparent: true,
      frame: false
    };

    const browserWindow = new BrowserWindow(options);
    const webContents = browserWindow.webContents;

    let isUploading = false;
    let isCanceled = false;

    browserWindow.on('closed', () => {
      if (isUploading) {
        isCanceled = true;
        UI.message('The upload has been canceled!');
      }
    });

    webContents.on('did-finish-load', () => {
      browserWindow.show();
    });

    webContents.on('requestInitialData', () => {
      let document = require('sketch/dom').getSelectedDocument(); // eslint-disable-line

      const username = Settings.settingForKey(`${CONFIG.APP_KEY}_username`);
      const token = Settings.settingForKey(`${CONFIG.APP_KEY}_token`);
      const selectedProject = Settings.documentSettingForKey(document, `${CONFIG.APP_KEY}_selected_project_id`);
      const selectedSize = Settings.documentSettingForKey(document, `${CONFIG.APP_KEY}_selected_size`);

      const selectedLayers = toArray(document.selectedLayers);
      const filteredLayers = selectedLayers.filter(item => item.type === 'Artboard');

      const params = {
        selectedProject: selectedProject,
        selectedSize: selectedSize,
        exportCount: filteredLayers.length,
      };

      if (username !== undefined && username !== '' && token !== undefined && token !== '') {
        params.token = token;
      }

      webContents
        .executeJavaScript(`initialData('${JSON.stringify(params)}')`)
        .catch(() => {});
    });

    webContents.on('openUrl', (s) => {
      NSWorkspace.sharedWorkspace().openURL(NSURL.URLWithString(s));
    });

    webContents.on('saveSettingsForProject', (s) => {
      let data = JSON.parse(s);
      if (data.selectedProject && data.selectedSize) {
        Settings.setDocumentSettingForKey(document, `${CONFIG.APP_KEY}_selected_project_id`, data.selectedProject);
        Settings.setDocumentSettingForKey(document, `${CONFIG.APP_KEY}_selected_size`, data.selectedSize);
      }
    });

    webContents.on('upload', (s) => {
      isCanceled = false;
      isUploading = true;

      const data = JSON.parse(s);
      if (data.selectedProject && data.selectedSize && username !== undefined && username !== '' && token !== undefined && token !== '') {
        const document = require('sketch/dom').getSelectedDocument();
        const selectedLayers = toArray(document.selectedLayers);
        const filteredLayers = selectedLayers.filter(item => item.type === 'Artboard');

        let totalCount = filteredLayers.length;
        let totalCountPercentage = filteredLayers.length + 1;

        let statusData = {
          progress: Math.round(((1) * 100) / totalCountPercentage),
          status: isCanceled
        };
        webContents
          .executeJavaScript(`exportStatus('${JSON.stringify(statusData)}')`)
          .catch(console.error);

        let loadItems = (uploadStack, index, results, files, callback) => {

          if (isCanceled) {
            callback({
              type: 'final',
              results: results
            });
          } else {
            sketch.export(uploadStack[index], { output: getCacheDirectory(), 'use-id-for-name': true, scales: `${data.selectedSize}`, 'save-for-web': true });

            const fullPath = `${getCacheDirectory()}/${uploadStack[index].id}${(data.selectedSize > 1) ? '@'+data.selectedSize+'x':''}.png`;
            const savedArtboardPath = NSURL.fileURLWithPath(fullPath);

            files.push(savedArtboardPath);

            const imageBinary = readBinaryFile(savedArtboardPath);
            const imageBase64 = imageBinary.base64EncodedStringWithOptions(0);

            const body = JSON.stringify({
              "filename": `${uploadStack[index].name}${(data.selectedSize > 1) ? '@'+data.selectedSize+'x':''}`,
              "source": 'data:image/png;base64,' + imageBase64
            });

            fetch(`${CONFIG.API_URL_HTTPS}/projects/${data.selectedProject}/update`, {
              method: 'POST',
              headers: {
                'Accept': `application/json;version=${CONFIG.API_VERSION}`,
                'Content-Type': 'application/json',
                'User-Agent': `Sketch ${CONFIG.APP_VERSION}`,
                'X-AUTH-TOKEN': token,
              },
              body: body,
            })
              .then(response => {
                const statusData = {
                  progress: Math.round(((index+2) * 100) / totalCountPercentage),
                  status: isCanceled,
                };
                webContents
                  .executeJavaScript(`exportStatus('${JSON.stringify(statusData)}')`)
                  .catch(console.error);

                if (response.status === 400) {
                  callback({
                    type: 'authError',
                  });
                } else {
                  if (uploadStack[index]) {
                    results.push({
                      artboard: uploadStack[index].name,
                      status: (response.status === 204 || response.status === 200) ? 'uploaded' : 'failed',
                    });
                  }
                  if (uploadStack[index + 1]) {
                    loadItems(uploadStack, index + 1, results, files, callback);
                  } else {
                    callback({
                      type: 'final',
                      results,
                    });
                  }
                }
              }).catch(() => {
                callback({
                  type: 'final',
                  results,
                });
              });
          }
        };

        const results = [];
        const files = [];
        const fiberInstance = Fiber.createFiber();
        loadItems(filteredLayers, 0, results, files, (state) => {
          isUploading = false;
          files.forEach(item => {
            deleteFileByURL(item);
          });
          if (state.type === 'authError') {
            webContents
              .executeJavaScript('authError()')
              .catch(() => {});
          } else {
            const successfullyUploaded = state.results.filter(item => item.status === 'uploaded').length;
            let successData = {
              title: `${successfullyUploaded} of ${totalCount} files has been uploaded`,
              projectUrl: `https://drafta.co/${data.selectedProject}`
            };
            webContents
              .executeJavaScript(`success('${JSON.stringify(successData)}')`)
              .catch(() => {});
          }
          fiberInstance.cleanup();
        });
      }
    });

    webContents.on('cancelUploading', () => {
      isCanceled = true;
    });

    webContents.on('closeBrowserWindow', () => {
      browserWindow.hide();
    });

    browserWindow.loadURL(template);
  }
}

function getCacheDirectory() {
  const fileManager = NSFileManager.defaultManager();
  const cachesURL = fileManager.URLsForDirectory_inDomains(NSCachesDirectory, NSUserDomainMask).lastObject(); // eslint-disable-line
  return cachesURL.URLByAppendingPathComponent(CONFIG.APP_KEY).path();
}

function deleteFileByURL(url) {
  let fileManager = NSFileManager.defaultManager();
  let error;
  let deleted = fileManager.removeItemAtURL_error(url, error); // eslint-disable-line
}

let readBinaryFile = filepath => NSData.alloc().initWithContentsOfFile(filepath);

function toArray(nsArray) {
  return nsArray.map(el => el);
}
