import BrowserWindow from 'sketch-module-web-view';

import exportCommand from './export';
import CONFIG from './config';

const Settings = require('sketch/settings');
const template = require('../resources/settings.html');

export default function settings() {
  const options = {
    identifier: `${CONFIG.APP_KEY}.settings`,
    alwaysOnTop: true,
    width: 576,
    height: 396,
    show: false,
    transparent: true,
    frame: false,
  };

  const browserWindow = new BrowserWindow(options);
  const { webContents } = browserWindow;

  webContents.on('did-finish-load', () => {
    browserWindow.show();
  });

  webContents.on('openUrl', (s) => {
    NSWorkspace.sharedWorkspace().openURL(NSURL.URLWithString(s));
  });

  webContents.on('requestInitialData', () => {
    const username = Settings.settingForKey(`${CONFIG.APP_KEY}_username`);
    const token = Settings.settingForKey(`${CONFIG.APP_KEY}_token`);

    const params = {
      username: undefined,
    };

    if (username !== undefined && username !== '' && token !== undefined && token !== '') {
      params.username = username;
    }
    webContents
      .executeJavaScript(`initialData('${JSON.stringify(params)}')`)
      .catch(() => {});
  });

  webContents.on('saveUserAuthSettings', (s) => {
    const data = JSON.parse(s);
    if (data.username && data.token) {
      Settings.setSettingForKey(`${CONFIG.APP_KEY}_username`, data.username);
      Settings.setSettingForKey(`${CONFIG.APP_KEY}_token`, data.token);
    }

    if (Settings.settingForKey(`${CONFIG.APP_KEY}_export_after_login`)) {
      exportCommand();
      Settings.setSettingForKey(`${CONFIG.APP_KEY}_export_after_login`, false);
    }
  });

  webContents.on('clearUserAuthSettings', () => {
    Settings.setSettingForKey(`${CONFIG.APP_KEY}_username`, '');
    Settings.setSettingForKey(`${CONFIG.APP_KEY}_token`, '');
  });

  webContents.on('closeBrowserWindow', () => {
    browserWindow.hide();
  });

  browserWindow.loadURL(template);
}
